<template>
  <b-container class="dashboard">
    <RepoNav v-if="$route.name != 'Topic'"/> 

    <b-row>
      <b-col cols="4">
        <b-form-input 
          placeholder="Search issues"
          @keyup.enter="getIssues">
        </b-form-input>
      </b-col>
      <b-col cols="2" offset="6">
        <b-button block variant="success" @click="newIssue">New issue</b-button>
      </b-col>
    </b-row>

    <b-card no-body v-for="issue in issues" :key="issue">
      <b-card-body>
        <router-link :to="'/'+fullRepoName()+'/issues/'+issue.id">
          <h5 class="card-title">{{ issue.topic }}</h5>
        </router-link>
        <p class="card-text">
          {{'#'+issue.id+' opened on '+issue.date+' by '+issue.user}}
        </p>
      </b-card-body>
    </b-card>

  </b-container>
</template>

<script>
import RepoNav from "@/components/RepoNav";
import axios from 'axios';
export default {
  name: "Issues",
  data() {
    return {
      issues: [
        {topic: 'a topic', id: '11', date: '1 April', user: 'Alice'},
        {topic: 'another topic', id: '12', date: '2 April', user: 'Bob'}
      ]
    };
  },
  methods: {
    fullRepoName() {
      return this.$route.params.owner + "/" + this.$route.params.repo;
    },
    getIssues() {
      axios({
        method: 'GET',
        params: '/'+this.fullRepoName()+'/issues',
        data: {
          owner: this.$route.params.owner,
          repo: this.$route.params.repo
        }
      }).then(response => {
        if(response.data.state == 'ok') {
          this.issues = response.data.issues;
        }else {
          console.log(response.data.error);
        }
      });
    },
    newIssue() {
      this.$router.replace('/'+this.fullRepoName()+'/issues/new');
    }
  },
  created() {
    axios({
      method: 'GET',
      url: '/'+this.fullRepoName()+'/issues',
      params: {
        ajax: 1,
        owner: this.$route.params.owner,
        repo: this.$route.params.repo
      }
    }).then(response => {
      if(response.data.state == 'ok') {
        this.issues = response.data.issues;
      }else {
        console.log(response.data.error);
      }
    });
  },
  components: { RepoNav }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.dashboard > :not(first-child) {
  margin-top: 1%;
}

a {
  text-decoration: none;
  color: #222;
}
a:hover {
  text-decoration: none;
  color: #0366D9;
}
</style>
